import React, { Component } from 'react'
import { FaAllergies } from 'react-icons/fa';
import { ThemeProvider } from 'styled-components';
import items from './data'
import client from'./Contentful'

const RoomContext = React.createContext();
class RoomProvider extends Component {
    state = {
        rooms:[],
        sortedRooms:[],
        featuredRooms:[],
        loading: true.slug,
        type: 'all',
        capacity: 1,
        price: 0,
        minPrice: 0,
        maxPrice: 0,
        minSize : 0,
        maxSize: 0,
        breakfast: false,
        pets: false

    };

    //get Data fom contentful
    getData = async ( ) => {
        try{
            let response = await client.getEntries({
                    content_type: "resort",

                    //order b price with room type
                    order: 'sys.createdAt'
                    // order by price
                    //order : "fields.price"
            })
              //this.getData
        let rooms = this.formateData(response.items)
        let featuredRooms = rooms.filter(room => room.featured === true );
        let maxPrice = Math.max(...rooms.map(item => item.price));
        let maxSize = Math.max(...rooms.map(item => item.size));
        this.setState({
           rooms, 
           featuredRooms,
           sortedRooms : rooms,
           loading: false,
           price: maxPrice,
           maxPrice,
           maxSize
        })
        }catch(error){
            console.log(error)
        }
    }

    componentDidMount(){
      this.getData()
    }

    formateData(items){
            let tempItems = items.map(item =>{
            let id = item.sys.id
            let images = item.fields.images.map (
                image => image.fields.file.url);
            let room = {...item.fields, images, id}
            return room;
        })
        return tempItems;
    }

    getRoom = (slug) => {
            let tempRooms = [...this.state.rooms];
            const room = tempRooms.find(room => room.slug === slug)
            return room;
    };

    handleChange = event =>{
        const target = event.target
        const value = target.type === 'checkbox' ? target.checked : target.value
        const name = event.target.name

        this.setState (
            {
            [name] : value
            },  this.filerRooms
        );
    };

filerRooms = () =>{
let {
        rooms,
        type,
        capacity,
        price,
        minSize,
        maxSize,
        breakfast,
        pets
    } = this.state
    //all the rooms
    let tempRooms = [...rooms];

    //transform values
    capacity  = parseInt(capacity)
    price  = parseInt(price)



//filter by type
    if(type != 'all'){
        tempRooms = tempRooms.filter(room => room.type === type)
    }

    //filter by capacity 
if(capacity != 1){
    tempRooms = tempRooms.filter(room => room.capacity >= capacity);
}

  //filter by price
tempRooms = tempRooms.filter(room => room.price <= price )

//filter by minsize and maxsize
tempRooms = tempRooms.filter(room => room.size >= minSize && room.size<= maxSize)

//filter by breakfast and pets
if(breakfast){
tempRooms = tempRooms.filter(room => room.breakfast === true )
}

if(pets){
    tempRooms = tempRooms.filter(room => room.pets === true )
    }
//chnage state
    this.setState({
        sortedRooms : tempRooms
    })
}

    render() {
        return  <RoomContext.Provider  value = {{...this.state, 
            getRoom : this.getRoom, 
            handleChange : this.handleChange}}>
            {this.props.children}
        </RoomContext.Provider>
    }
}

const RoomConsumer = RoomContext.Consumer;

export function withRoomConsumer (Component){
    return function ConsumerWrapper(props){
        return <RoomConsumer>
            {value => <Component {...props} context ={value} />}
        </RoomConsumer>
    }
}
export  {  RoomProvider, RoomConsumer, RoomContext };


